<template>
    <div>
        <template v-for="(item, index) in images">
            <div><span class="headline">{{ item.title }}</span><br/><span style="font-size:15px;color:grey">{{ item.time }}</span></div>
            <Gallery :images="item.images" :imageWidth="720" :popUpMaxWidth="720" :mdCols="4" :smCols="4" :xsCols="6"
                :lgCols="2"></Gallery>
        </template>
    </div>
</template>
<script >
import Gallery from './Gallery.vue'
export default {
    components: {
        Gallery
    },
    data() {
        return {
            images: []
        }
    },
    mounted() {
        this.$loveData.loveline.filter(i => i.images).forEach(element => {
            const images = []
            element.images.forEach((ele, index) => {
                images.push({ id: element.title + index, imgSrc: require('@/assets/images/' + ele) })
            })
            this.images.push({ title: element.title, time: element.datetime + '/' + (element.endtime || element.datetiem), images: images })
        })
    }
}
</script>