<template>
    <div>
        <v-row justify="space-around">
            <v-col cols="12">
                <v-sheet tile height="64" class="d-flex">
                    <v-toolbar flat>
                        <v-btn fab text small color="grey darken-2" @click="$refs.calendar.prev()">
                            <v-icon small>
                                mdi-chevron-left
                            </v-icon>
                        </v-btn>
                        <v-btn fab text small color="grey darken-2" @click="$refs.calendar.next()">
                            <v-icon small>
                                mdi-chevron-right
                            </v-icon>
                        </v-btn>
                        <v-toolbar-title>
                            {{ calendarTitle }}
                        </v-toolbar-title>
                        <v-spacer></v-spacer>
                    </v-toolbar>
                </v-sheet>
                <v-sheet height="600">
                    <v-calendar ref="calendar" v-model="calVal" :weekdays="[0, 1, 2, 3, 4, 5, 6]" color="#ed0025"
                        locale="zh" type="month" event-overlap-mode="stack" :event-overlap-threshold="30"
                        :events="events">
                    </v-calendar>
                </v-sheet>
            </v-col>
        </v-row>
    </div>
</template>
<script>
export default {
    data() {
        return {
            calVal: '2020-10-01',
            events: [],
            calendarTitle: '',
            color: {
                together: '#FF4081',
                travel: '#009688',
                memorial: '#03A9F4',
                birth: '#EF6C00',
                breal:'#263238'
            }
        }
    },
    mounted() {
        this.calendarTitle = this.$refs.calendar.title
        this.$watch(
            () => {
                return this.$refs.calendar.title
            },
            (val) => {
                this.calendarTitle = val
            }
        )
        this.$refs.calendar.checkChange()
        this.$loveData.loveline.forEach(element => {
            if (element.endtime) {
                element.end = element.endtime
            }
            element.start = element.datetime
            element.name = element.title
            element.color = this.color[element.type]
            element.timed = true
            this.events.push(element)
        });
    },
    methods: {
        setToday() {
            this.calVal = ''
        },
    }
}
</script>