<template>
    <div>
        <v-app>
            <v-container data-app="true">
                <v-navigation-drawer permanent mini-variant left app>
                    <v-list>
                        <v-list-item>
                            <v-list-item-icon @click="chosen = 1">
                                <v-icon color="#ed0025">mdi-heart-multiple</v-icon>
                            </v-list-item-icon>
                        </v-list-item>
                        <v-list-item @click="chosen = 2">
                            <v-list-item-icon>
                                <v-icon color="#ed0025">mdi-calendar-heart-outline</v-icon>
                            </v-list-item-icon>
                        </v-list-item>
                        <v-list-item @click="chosen = 3">
                            <v-list-item-icon>
                                <v-icon color="#ed0025">mdi-camera-iris</v-icon>
                            </v-list-item-icon>
                        </v-list-item>
                    </v-list>
                </v-navigation-drawer>
                <v-main>
                    <template v-if="chosen == 1">
                        <v-row justify="space-around" style="min-height:100px;">
                            <v-col :md="4" :sm="4" :cols="6" :lg="2">
                                <v-card shaped>
                                    <v-card-title><span class="card-title">我们相爱</span></v-card-title>
                                    <v-card-text style="text-align:center"><span class="card-num">{{day}}</span><span
                                            class="card-unit">天</span>
                                    </v-card-text>
                                </v-card>
                            </v-col>
                            <v-col :md="4" :sm="4" :cols="6" :lg="2">
                                <v-card shaped>
                                    <v-card-title><span class="card-title">说了晚安</span></v-card-title>
                                    <v-card-text style="text-align:center"><span class="card-num">1300</span><span
                                            class="card-unit">次</span></v-card-text>
                                </v-card>
                            </v-col>
                            <v-col :md="4" :sm="4" :cols="6" :lg="2">
                                <v-card shaped>
                                    <v-card-title><span class="card-title">聊天记录</span></v-card-title>
                                    <v-card-text style="text-align:center"><span class="card-num">4.5</span><span
                                            class="card-unit">G</span></v-card-text>
                                </v-card>
                            </v-col>
                            <v-col :md="4" :sm="4" :cols="6" :lg="2">
                                <v-card shaped>
                                    <v-card-title><span class="card-title">打了电话</span></v-card-title>
                                    <v-card-text style="text-align:center"><span class="card-num">400</span><span
                                            class="card-unit">余次</span></v-card-text>
                                </v-card>
                            </v-col>
                        </v-row>
                        <v-row justify="space-around">
                            <v-col cols="10" :md="8">
                                <v-card shaped>
                                    <v-card-title>我们在哪里</v-card-title>
                                    <v-card-text>
                                        <div id="map" style="width:100%;height:400px">地图</div>
                                    </v-card-text>

                                </v-card>
                            </v-col>
                        </v-row>
                        <v-row justify="space-around">
                            <v-col cols="10" :md="8">
                                <v-card shaped>
                                    <v-card-title>我们的恋爱线</v-card-title>
                                    <v-card-text>
                                        <v-timeline>
                                            <v-virtual-scroll :items="story" height="1300" item-height="400">
                                                <template v-slot:default="{ item, index }">
                                                    <v-timeline-item :key="index" small :color="color[item.type]"
                                                        fill-dot icon="mdi-star" class="mb-6">
                                                        <v-card dark :color="color[item.type]" shaped flat outlined
                                                            class="mx-auto">
                                                            <v-card-title class="" style="font-size:20px">
                                                                {{ item.title }}
                                                            </v-card-title>
                                                            <v-card-text class="white text--primary"
                                                                v-if="item.desc.length < 10" style="font-size:15px">
                                                                {{ item.desc || '想说但是说不出来' }}
                                                            </v-card-text>
                                                            <v-card-text class="white text--primary" v-else
                                                                style="font-size:15px">
                                                                <v-tooltip>
                                                                    <template v-slot:activator="{ on, attrs }">
                                                                        <span v-bind="attrs" v-on="on">
                                                                            {{ item.desc.slice(0, 10) + '...' }}</span>
                                                                    </template>
                                                                    <span>
                                                                        <span style="font-size: 20px;"></span>{{
                                                                                item.datetime + '：' + item.desc
                                                                        }}</span>
                                                                </v-tooltip>
                                                            </v-card-text>
                                                        </v-card>
                                                    </v-timeline-item>
                                                </template>
                                            </v-virtual-scroll>
                                        </v-timeline>
                                    </v-card-text>
                                </v-card>
                            </v-col>
                        </v-row>
                    </template>
                    <LoveCalendarVue v-if="chosen == 2"></LoveCalendarVue>
                    <ImagesVue v-if="chosen == 3"></ImagesVue>
                </v-main>
            </v-container>
        </v-app>
    </div>
</template>
<script>
import LoveCalendarVue from './LoveCalendar.vue';
import ImagesVue from './Images.vue';
export default {
    components: { LoveCalendarVue, ImagesVue },
    data() {
        return {
            chart: null,
            chosen: 1,
            day:0,
            geoCoordMap: {
                '上海': [121.4648, 31.2891],
                '北京': [116.4551, 40.2539],
                '恩施': [109.4881, 30.2721],
                '宝鸡': [107.1826, 34.3433],
                '成都': [103.9526, 30.7617],
                '杭州': [119.5313, 29.8773],
                '武汉': [114.3896, 30.6628],
                '苏州': [120.6519, 31.3989],
                '西安': [109.1162, 34.2004],
                '重庆': [107.7539, 30.1904],
                '长沙': [113.0823, 28.2568],
            },
            color: {
                together: '#FF4081',
                travel: '#009688',
                memorial: '#03A9F4',
                birth: '#EF6C00',
                breal:'#263238'
            }
        }
    },
    methods: {

        initEcharts() {
            this.chart = this.$echarts.init(document.getElementById('map'));

            let BJData = [];
            this.$loveData.loveline.filter(i => i.type == 'travel').forEach(ele => {
                BJData.push({ name: ele.travelArea, value: ele.datetime })
            })

            var planePath = 'path://M1705.06,1318.313v-89.254l-319.9-221.799l0.073-208.063c0.521-84.662-26.629-121.796-63.961-121.491c-37.332-0.305-64.482,36.829-63.961,121.491l0.073,208.063l-319.9,221.799v89.254l330.343-157.288l12.238,241.308l-134.449,92.931l0.531,42.034l175.125-42.917l175.125,42.917l0.531-42.034l-134.449-92.931l12.238-241.308L1705.06,1318.313z';
            var color = ['#00BFFF', '#48D1CC'];
            const _this = this
            var series = [];
            ['我的', '宝宝'].forEach(function (item, i) {
                series.push({
                    name: item,
                    type: 'lines',
                    zlevel: 1,
                    effect: {
                        show: true,
                        period: 6,
                        trailLength: 0.7,
                        color: '#fff',
                        symbolSize: 3
                    },
                    lineStyle: {
                        normal: {
                            color: color[i],
                            width: 0,
                            curveness: 0.2
                        }
                    },
                    data: _this.convertData(BJData, i)
                }, {
                    name: item,
                    type: 'lines',
                    zlevel: 2,
                    effect: {
                        show: true,
                        period: 6,
                        trailLength: 0,
                        symbol: planePath,
                        symbolSize: 15
                    },
                    lineStyle: {
                        normal: {
                            color: color[i],
                            width: 1,
                            opacity: 0.4,
                            curveness: 0.2
                        }
                    },
                    data: _this.convertData(BJData, i)
                }, {
                    name: item,
                    type: 'effectScatter',
                    coordinateSystem: 'geo',
                    zlevel: 2,
                    rippleEffect: {
                        brushType: 'stroke'
                    },
                    label: {
                        normal: {
                            show: true,
                            position: 'right',
                            formatter: '{b}'
                        }
                    },
                    symbolSize: function (val) {
                        return val[2] / 8;
                    },
                    itemStyle: {
                        normal: {
                            color: color[i]
                        }
                    },
                    data: BJData.map(function (dataItem) {
                        return {
                            name: dataItem.name,
                            value: _this.geoCoordMap[dataItem.name].concat([dataItem.value])
                        };
                    })
                });
            });

            let option = {
                tooltip: {
                    trigger: 'item'
                },
                geo: {
                    map: 'china',
                    label: {
                        emphasis: {
                            show: false
                        }
                    },
                    roam: true,
                    itemStyle: {
                        normal: {
                            areaColor: '#FFFAF0',
                            borderColor: '#191970'
                        },
                        emphasis: {
                            areaColor: '#F5F5F5'
                        }
                    }
                },
                series: series
            };
            this.chart.setOption(option);
        },

        convertData(data, index) {
            const fromPlace = ['西安', '恩施']
            var res = [];
            for (var i = 0; i < data.length; i++) {
                var dataItem = data[i];
                var fromCoord = this.geoCoordMap[fromPlace[index]];
                var toCoord = this.geoCoordMap[dataItem.name];
                if (fromCoord && toCoord) {
                    res.push([{
                        coord: fromCoord
                    }, {
                        coord: toCoord
                    }]);
                }
            }
            return res;
        }
    },
    computed: {
        story() {
            return this.$loveData.loveline.reverse()
        }
    },
    mounted() {
        var current = Date();
        var seconds = (Date.parse(current) - Date.parse('2020-10-01')) / 1000;
        this.day = Math.floor(seconds / (3600 * 24));
        this.$nextTick(() => {
            this.initEcharts();
        });
    }
}
</script>
<style>
.theme--light.v-card>.v-card__text {
    color: rgb(255 255 255);
}

.theme--light.v-card {
    background-color: #ed0025;
    color: rgb(255 255 255 / 100%);
}

.card-title {
    font-family: monospace;
    font-size: 40px;
    text-align: center;
}

.card-num {
    font-family: monospace;
    font-style: oblique;
    font-weight: bold;
    font-size: 55px;
}

.card-unit {
    font-size: 25px;
    margin-left: 15px;
}

.v-card__text {
    text-align: center;
    width: 100%;
}

.v-timeline:not(.v-timeline--dense):not(.v-timeline--reverse) .v-timeline-item--after .v-timeline-item__body,
.v-timeline:not(.v-timeline--dense):not(.v-timeline--reverse) .v-timeline-item:nth-child(odd):not(.v-timeline-item--before) .v-timeline-item__body {
    max-width: 100%;
}
</style>